* {
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  font-size: 16px;
}

.root {
  text-align: center;
}

h1  {
  text-align: center;
  padding: 20px;
}

p {
  font-color: #333;
  width: 70%;
  margin: 0 auto;
  text-align: center;
}

.grid-select {
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 70%;
  text-align: center;
}

.grid-select label {
  line-height: 1.4rem;
  font-size: 1.1rem;
  padding-right: 10px;
}

.grid-select select {
  height: 3rem;
  padding: 10px;
}

.game {
  margin: 0 auto;
  font-family: Arial, sans-serif;
  box-sizing: border-box;
  border: 1px solid #333;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background: #ccc;
  margin-bottom: 20px;
}

.game.disabled {
  pointer-events: none;
}

.game.type3-3 {
  width: 302px;
  height: 302px;
}

.game.type4-4 {
  width: 402px;
  height: 402px;
}

.game.type5-5 {
  width: 502px;
  height: 502px;
}

.game.type6-6 {
  width: 602px;
  height: 602px;
}

.game.type7-7 {
  width: 702px;
  height: 702px;
}

.game.type8-8 {
  width: 802px;
  height: 802px;
}

.game.type9-9 {
  width: 902px;
  height: 902px;
}

.game.type10-10 {
  width: 1002px;
  height: 1002px;
}

.square {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 1px solid #333;
  text-align: center;
  line-height: 100px;
  font-size: 2rem;
  font-weight: bold;
  cursor: pointer;
}

.square.disabled {
  cursor: default;
  pointer-events: none;
}

.square.win {
  background: #209618;
  color: white;
}

.bt-reset {
  display: block;
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 20px 40px;
}

.result-display {
  text-align: center;
  font-size: 1.6rem;
  width: 100%;
  padding-top: 40px;
}
